import React, { useState } from 'react';
import './App.css';
import NavBar from './navBar'

// this is the list of available pages on the website
const pages = [ 'Projects', 'About me', 'Home' ];

const App = () =>{
  // use function hook
  const [mainContent, mainUpdater] = useState('Home');

  const handleClick = (page) => {
    console.log(page);
    mainUpdater(page);
  };

  let mainElt = null; // place hold for the main element

  if(mainContent === 'Home') {
    // the element will be the main page
    mainElt = (
      <div className="pic1 bg">
        <div className = "title_box">
          <span className = "title">Hello! Welcome</span><br/>
        </div>
      </div>
    );

  } else if(mainContent === 'About me') {
    // the element will be the about-me page
    mainElt = (
      <div className="pic2 bg" >
        <div className="txtBox container_flex">
          <div className="flex_4">
            <p>My pic</p>
          </div>

          <div className="flex_6">
            <p>Hi, I'm Qiuyu Shi. I major in Computer Science at
            University of Michigan.</p>
          </div>
        </div>
    </div>
    );

  } else {
    // the element will be the projects page
    mainElt = (
      <div className="pic3 bg">
        <div className="txtBox">
        </div>
      </div>);
  }

  return (
    <div id='App' 
      style={{height: window.innerHeight * 1.2, 
      width: window.innerWidth}}>
      <NavBar handleClick={handleClick} pages={pages}></NavBar>

      {mainElt}
    </div>
  );
}

export default App;
