import React from 'react'
import NavItem from './navItem';

function NavBar(props) {
  return(
    <nav className = "menu">
      <ul>
        {
          props.pages.map((item, i) => (
              <NavItem handleClick={props.handleClick} key={i}>
                {item}
              </NavItem>)
          )
        }
      </ul>
    </nav>
  );
}

export default NavBar;
