import React from 'react'

class NavItem extends React.Component {
  constructor(props) {
    super(props);
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick() {
    this.props.handleClick(this.props.children);
  }

  render() {
    return(
      <li onClick={this.handleClick}>
        {this.props.children}
      </li>
    );
  }
};

export default NavItem;